*{
    padding: 0px;
    margin: 0px;
    box-sizing: border-box;
}
body{
        background: url('https://cdn.pixabay.com/photo/2015/03/26/09/47/sky-690293_960_720.jpg');
        background-size: 100% 100vh;
        background-repeat: no-repeat;
}
.main-temp{
    font-size: 7rem;
}
.main-div{
    margin-top: 21rem;
    opacity: 1;
    z-index: 9;
}
.card{
    box-shadow: 12px 12px 13px rgba(0,0,0,0.5);
    margin-top: 10%;
}
.form_class{
    display: flex;
    justify-content: center;
    margin-top: 5px;
    border: none;
}
.form_class input{
    padding: 10px 25px;
    border-radius: 6px;
    text-transform: capitalize;
}